.nav {
  background: rgb(83, 83, 83);
  box-shadow: 0px 1px 3px rgb(50, 180, 100);
  margin: 0 0 1rem 0;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 0.5rem;
  padding: 1rem;
  justify-content: space-around;
  align-items: center;
  margin: 0;
}

.nav-list-item a {
  display: block;
  padding: 0.25rem;
  font-weight: 700;
  color: white;
  text-decoration: none;
}

.nav-list-spacer {
  display: block;
  height: 1.5rem;
  width: 0.125rem;
  width: 1px;
  background: rgb(90, 180, 100);
}
